.movie {
    border: 1px solid #e0e0e0; 
    border-radius: 8px; /* 모서리 동그랗게하기 */
    overflow: hidden; 
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); 
    margin-bottom: 20px;
    background: white; 
  }
  
  .movie img {
    width: 100%; 
    height: auto; 
    display: block; 
  }
  
  .movie p {
    padding: 0 10px;
    color: #666; 
    font-size: 0.9em;
    line-height: 1.6;
  }

  .movie h2 {
    margin: 0.5rem 0;
    font-size: 1.5rem;
  }
  
  .movie ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .movie li {
    background-color: #eee;
    padding: 0.25rem 0.5rem;
    border-radius: 0.25rem;
  }


  
  